@font-face {
  font-family: 'futura_bk_btbook';
  src: url('../../fontes/futura/FutuBk__-webfont.eot');
  src: url('../../fontes/futura/FutuBk__-webfont.eot?#iefix') format('embedded-opentype'), url('../../fontes/futura/FutuBk__-webfont.woff2') format('woff2'), url('../../fontes/futura/FutuBk__-webfont.woff') format('woff'), url('../../fontes/futura/FutuBk__-webfont.ttf') format('truetype'), url('../../fontes/futura/FutuBk__-webfont.svg#futura_bk_btbook') format('svg');
