}
.font-arial-narrow {
  font-family: 'Arial Narrow', Arial, sans-serif;
}
.font-cuprum {
  font-family: 'Cuprum', sans-serif;
}
.font-size-16 {
  font-size: 16px;
}
body,
label,
.checkbox label {
  font-weight: 300;
}
body * {
  box-sizing: border-box;
}
.btn {
  font-family: 'Arial Narrow', Arial, sans-serif;
  text-decoration: none;
}
.btn-amarelo {
  border: none;
  color: #000000;
  background-color: #ffd500;
  min-width: 154px;
}
.btn-preto {
  border: none;
  color: #fff;
  background-color: #000000;
  min-width: 154px;
}
.btn-preto:hover,
.btn-preto:focus,
.btn-preto:active {
  color: #fff;
}
.btn-cinza {
  border: none;
  color: #000000;
  background-color: #d1d1d1;
  min-width: 154px;
}
.btn-cinza:hover,
.btn-cinza:focus,
.btn-cinza:active {
  color: #000000;
}
.btn-auto-width {
  min-width: 10px;
}
.btn-extra-pequeno {
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 12px;
  line-height: 12px;
  min-width: 0;
}
.btn-pequeno {
  padding-top: 8px;
  padding-bottom: 8px;
  font-size: 13px;
  line-height: 13px;
}
.btn-meio-medio {
  padding-top: 12px;
  padding-bottom: 10px;
  font-size: 12px;
  vertical-align: middle;
  line-height: 12px;
}
.btn-medio {
  padding-top: 12px;
  padding-bottom: 12px;
  font-size: 12px;
}
.btn-grande {
  font-size: 15px;
  padding-top: 14px;
  padding-bottom: 14px;
}
.btn-estreito {
  width: auto;
  min-width: 0px;
}
.text-right {
  text-align: right;
}
.newsletter-gerada {
  font-family: Arial, sans-serif;
  font-size: 17px;
  max-width: 700px;
}
.newsletter-gerada h1 {
  font-size: 20px;
  vertical-align: top;
  margin-top: 0;
  margin-bottom: 0;
}
.newsletter-gerada h1 img {
  width: 10%;
  float: left;
  display: inline-block;
  margin-right: 2%;
}
.newsletter-gerada h1 span {
  vertical-align: top;
  width: 88%;
  display: inline-block;
  line-height: 100%;
}
.newsletter-gerada span.sub-infos {
  position: relative;
  float: left;
  font-size: 15px;
  margin-bottom: 30px;
}
.newsletter-gerada .box-post {
  margin-bottom: 30px;
}
.newsletter-gerada .box-post.col-sm-6:nth-child(1) {
  padding-right: 15px;
}
.newsletter-gerada .box-post.col-sm-6:nth-child(2) {
  padding-left: 15px;
}
.newsletter-gerada .box-post .box-img-post-destaque {
  background-color: #f8f8f8;
}
.newsletter-gerada .box-post .img-post-destaque {
  width: 100%;
  border-top: 20px solid black;
}
.newsletter-gerada .box-post h2 {
  font-family: 'Arial Narrow', Arial, sans-serif;
  font-size: 24px;
  font-weight: bold;
}
.newsletter-gerada.email h1 {
  font-size: 24px;
  line-height: 17px;
  vertical-align: top;
}
.newsletter-gerada.email h1 img {
  width: 80px;
}
/* Sticky footer styles
-------------------------------------------------- */
html {
  position: relative;
  min-height: 100%;
}
/*-------------------------------------------------- */
.footer {
  font-family: 'Cuprum', sans-serif;
  font-size: 13px;
  font-weight: bold;
}
#rodape-link-lewlara {
  padding-right: 2%;
}
#rodape-quadrado-preto {
  background-color: black;
  width: calc(100% - 20px);
  height: 80px;
  display: block;
  margin-top: 10px;
  padding-top: 20px;
}
#rodape-quadrado-preto img {
  margin-top: 10px;
  margin-right: 10px;
}
